.ActivityAndInsights {
    background-color: rgba(var(--center-channel-color-rgb), 0.04);

    .insights-body {
        padding: 24px;

        .top-channel-container {
            display: flex;

            .top-channel-line-chart {
                flex: 0.67;
                margin-right: 16px;
            }

            .top-channel-list {
                flex: 0.33;
                margin-left: 16px;

                .top-channel-row {
                    display: flex;
                    align-items: center;
                    padding: 12px 0;

                    &:nth-of-type(2) {
                        .skeleton-loader {
                            &.title {
                                flex: 0.85;
                            }
                        }
                    }

                    &:nth-of-type(3) {
                        .skeleton-loader {
                            &.title {
                                flex: 0.9;
                            }
                        }
                    }

                    &:nth-of-type(4) {
                        .skeleton-loader {
                            &.title {
                                flex: 0.5;
                            }
                        }
                    }

                    &:nth-of-type(5) {
                        .skeleton-loader {
                            &.title {
                                flex: 0.7;
                            }
                        }
                    }

                    .skeleton-loader {
                        &.title {
                            margin-left: 8px;
                        }
                    }
                }
            }
        }

        .top-reaction-container {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 18px;

            .bar-chart-entry {
                display: flex;
                flex: 0.2;
                flex-direction: column;
                align-items: center;
                margin-top: auto;

                &:nth-of-type(1) {
                    .skeleton-loader {
                        &.bar-chart {
                            height: 140px;
                        }
                    }

                    .bar-chart-data {
                        background-color: var(--button-bg);
                    }
                }

                &:nth-of-type(2) {
                    .bar-chart-data {
                        background-color: var(--online-indicator);
                    }
                }

                &:nth-of-type(3) {
                    .skeleton-loader {
                        &.bar-chart {
                            height: 140px;
                        }
                    }

                    .bar-chart-data {
                        background-color: var(--dnd-indicator);
                    }
                }

                &:nth-of-type(4) {
                    .skeleton-loader {
                        &.bar-chart {
                            height: 120px;
                        }
                    }

                    .bar-chart-data {
                        background-color: var(--new-message-separator);
                    }
                }

                &:nth-of-type(5) {
                    .skeleton-loader {
                        &.bar-chart {
                            height: 140px;
                        }
                    }

                    .bar-chart-data {
                        background-color: var(--away-indicator);
                    }
                }

                .reaction-count {
                    margin-bottom: 6px;
                    font-family: 'Open Sans';
                    font-size: 11px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 16px;
                }

                .skeleton-loader {
                    &.bar-chart {
                        margin-bottom: 6px;
                    }
                }

                .bar-chart-data {
                    width: 8px;
                    margin-bottom: 6px;
                    border-radius: 6px;
                }
            }
        }

        .empty-state {
            display: flex;
            flex: 1;
            flex-direction: column;
            align-items: center;
            margin: 70px 0;

            .empty-state-emoticon {
                display: flex;
                padding: 10px;
                margin-bottom: 8px;
                background: rgba(var(--center-channel-color-rgb), 0.08);
                border-radius: 50px;

                i {
                    color: rgba(var(--center-channel-color-rgb), 0.56);
                }
            }

            .empty-state-text {
                max-width: 120px;
                color: rgba(var(--center-channel-color-rgb), 0.72);
                font-family: 'Open Sans';
                font-size: 12px;
                font-style: normal;
                font-weight: 400;
                line-height: 16px;
                text-align: center;
            }
        }
    }
}
